nav {
  ul {
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
    gap: 20px;
    list-style: none;

    a {
      text-decoration: none;
      color: black;
      text-transform: uppercase;

      &:hover {
        text-decoration: underline;
      }
    }
  }
}
